import React from 'react';

import DIV from '@vl/redata/DIV.macro';
import { ctx } from '@vl/redata';
import _ from 'lodash';
import { Row, Col } from 'antd';
import gstyles from '@vl/gstyles';
import { Link } from '@uz/unitz-components-web/Link';

const View8 = () => {
  return (
    <DIV className="FilterCategories">
      {!!ctx.get('categoryOptions.length') && (
        <div className="bg-background1 py-6 sm:py-16">
          <div className="text-brandb500 px-6 sm:px-0 font-semibold sm:font-medium text-xl sm:text-4xl text-center mb-3">
            {ctx.apply('i18n.t', 'Category.suggestionCategoriesTitle')}
          </div>
          <div className="wrapper app-row">
            <Row gutter={[24, 24]}>
              {ctx.get('categoryOptions', []).map((item) => {
                return (
                  <Col lg={8} md={12} sm={24} xs={12} key={item.id}>
                    <Link to={ctx.apply('routeStore.toUrl', 'category', item)}>
                      <div className="py-4 text-center rounded-xl bg-transparent sm:bg-brandb-lightest hover:bg-brandb-lighter">
                        <div className="flex justify-center align-center mb-3">
                          {gstyles.icons({
                            name: _.get(item, 'icon', ''),
                            size: 48,
                            fill: gstyles.colors.brandb500,
                          })}
                        </div>
                        <p className="text-brandb500 font-semibold text-sm sm:text-xl">
                          {_.get(item, 'display_name', '')}
                        </p>
                      </div>
                    </Link>
                  </Col>
                );
              })}
            </Row>
          </div>
        </div>
      )}
    </DIV>
  );
};

export default View8;
