import { bindings, hook } from '@vl/redata';
import _ from 'lodash';
import useRoute from '@vl/hooks/useGbRoute';

const bindData = bindings({
  FilterCategories: {
    rules: [
      [
        'data',
        {
          data: {
            categoryOptions: hook((ctx) => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              const categories = ctx.get('categories', []);
              const data = _.compact(_.castArray(_.get(routeParams, 'id', '')));
              const filterCategories = categories.filter((val) => !data.includes(val.id));
              return filterCategories;
            })
          }
        }
      ]
    ]
  }
});

export default bindData;
